  max-width: 800px;
  margin: 32px auto;
  padding: 0 20px;
  width: 100%;
  box-sizing: border-box;
}

/* 过滤器样式 */
.filters {
  margin-bottom: 24px;
  width: 100%;
}

.filters::part(base) {
  width: 100%;
  margin: 0;
  padding: 0;
  background: var(--sl-color-neutral-50);
  border: 1px solid var(--sl-color-neutral-200);
}

.filters::part(body) {
  padding: 24px;
  width: 100%;
  box-sizing: border-box;
}

.filter-group {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.filter-group sl-input {
  flex: 2;
}

.filter-group sl-select {
  flex: 1;
}

.filter-group sl-input[type="datetime-local"] {
  flex: 1;
}

.date-separator {
  color: var(--sl-color-neutral-400);
  padding: 0 0.25rem;
}

/* 记录列表样式 */
.records-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
}

.record-card {
  width: 100%;
}

.record-card::part(base) {
  width: 100%;
  margin: 0;
  border: 1px solid var(--sl-color-neutral-200);
  transition: all 0.2s ease;
}

.record-card:hover::part(base) {
  transform: translateY(-2px);
  box-shadow: var(--sl-shadow-x-small);
}

/* 记录卡片样式 */
.record-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.record-time {
  color: var(--sl-color-neutral-600);
  font-size: 0.9rem;
}

.record-content {
  color: var(--sl-color-neutral-600);
  line-height: 1.6;
  margin: 12px 0;
}

/* Markdown内容样式 */
.record-content h1,
.record-content h2,
.record-content h3,
.record-content h4,
.record-content h5,
.record-content h6 {
  margin-top: 1.5em;
  margin-bottom: 0.5em;
  color: var(--sl-color-neutral-900);
}

.record-content h1:first-child,
.record-content h2:first-child,
.record-content h3:first-child {
  margin-top: 0;
}

.record-content p {
  margin: 0.8em 0;
}

.record-content ul,
.record-content ol {
  padding-left: 1.5em;
  margin: 0.8em 0;
}

.record-content li {
  margin: 0.3em 0;
}

.record-content code {
  background: var(--sl-color-neutral-100);
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-size: 0.9em;
  color: var(--sl-color-neutral-800);
}

.record-content pre {
  background: var(--sl-color-neutral-50);
  padding: 1em;
  border-radius: 6px;
  overflow-x: auto;
  margin: 1em 0;
}

.record-content pre code {
  background: none;
  padding: 0;
}

.record-content blockquote {
  border-left: 4px solid var(--sl-color-neutral-200);
  margin: 1em 0;
  padding-left: 1em;
  color: var(--sl-color-neutral-600);
}

.record-content a {
  color: var(--sl-color-primary-600);
  text-decoration: none;
}

.record-content a:hover {
  text-decoration: underline;
}

/* 记录底部样式 */
.record-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-left {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.record-actions {
  display: flex;
  gap: 0.5rem;
}

.record-actions sl-button::part(base) {
  width: 32px;
  height: 32px;
  padding: 0;
  border-radius: 50%;
  border: 2px solid;
  background: transparent;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

sl-tag {
  cursor: default;
}

/* 响应式布局 */
@media (max-width: 640px) {
  .filter-group {
    flex-direction: column;
    gap: 12px;
  }

  .filter-group sl-input,
  .filter-group sl-select {
    flex: none;
  }
} `, "",{"version":3,"sources":["webpack://./src/pages/history/style.css"],"names":[],"mappings":"AAAA;EACE,gBAAgB;EAChB,iBAAiB;EACjB,eAAe;EACf,WAAW;EACX,sBAAsB;AACxB;;AAEA,UAAU;AACV;EACE,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,WAAW;EACX,SAAS;EACT,UAAU;EACV,sCAAsC;EACtC,6CAA6C;AAC/C;;AAEA;EACE,aAAa;EACb,WAAW;EACX,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,kCAAkC;EAClC,kBAAkB;AACpB;;AAEA,WAAW;AACX;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,WAAW;AACb;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,WAAW;EACX,SAAS;EACT,6CAA6C;EAC7C,yBAAyB;AAC3B;;AAEA;EACE,2BAA2B;EAC3B,oCAAoC;AACtC;;AAEA,WAAW;AACX;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,kCAAkC;EAClC,iBAAiB;AACnB;;AAEA;EACE,kCAAkC;EAClC,gBAAgB;EAChB,cAAc;AAChB;;AAEA,iBAAiB;AACjB;;;;;;EAME,iBAAiB;EACjB,oBAAoB;EACpB,kCAAkC;AACpC;;AAEA;;;EAGE,aAAa;AACf;;AAEA;EACE,eAAe;AACjB;;AAEA;;EAEE,mBAAmB;EACnB,eAAe;AACjB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,uCAAuC;EACvC,oBAAoB;EACpB,kBAAkB;EAClB,gBAAgB;EAChB,kCAAkC;AACpC;;AAEA;EACE,sCAAsC;EACtC,YAAY;EACZ,kBAAkB;EAClB,gBAAgB;EAChB,aAAa;AACf;;AAEA;EACE,gBAAgB;EAChB,UAAU;AACZ;;AAEA;EACE,kDAAkD;EAClD,aAAa;EACb,iBAAiB;EACjB,kCAAkC;AACpC;;AAEA;EACE,kCAAkC;EAClC,qBAAqB;AACvB;;AAEA;EACE,0BAA0B;AAC5B;;AAEA,WAAW;AACX;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,WAAW;EACX,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,WAAW;AACb;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,UAAU;EACV,kBAAkB;EAClB,iBAAiB;EACjB,uBAAuB;EACvB,yBAAyB;EACzB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,eAAe;AACjB;;AAEA,UAAU;AACV;EACE;IACE,sBAAsB;IACtB,SAAS;EACX;;EAEA;;IAEE,UAAU;EACZ;AACF","sourcesContent":[".history-container {\n  max-width: 800px;\n  margin: 32px auto;\n  padding: 0 20px;\n  width: 100%;\n  box-sizing: border-box;\n}\n\n/* 过滤器样式 */\n.filters {\n  margin-bottom: 24px;\n  width: 100%;\n}\n\n.filters::part(base) {\n  width: 100%;\n  margin: 0;\n  padding: 0;\n  background: var(--sl-color-neutral